.checkbox-group {
  .checkbox-normal {
    display: none;
  }

  display: inline-block;
  position: relative;

  label {
    display: inline-block;
    position: relative;
    padding-left: 25px;
    cursor: pointer;
    font-size: 13px;
    line-height: 21px;
  }

  + .checkbox-group label {
    margin-left: 15px;
  }
}

.clearfix {
  &:before, &:after {
    content: "";
    display: table;
  }
}

.radio-group {
  label:before {
    content: "";
    display: inline-block;
    width: 16px;
    height: 16px;
    position: absolute;
    top: 3px;
    left: 0px;
    background: #fcfcfc;
    border: 1px solid #bbb;
  }

  .radio-normal {
    + label:before {
      -webkit-border-radius: 10px;
      -moz-border-radius: 10px;
      border-radius: 10px;
    }

    &:checked + label:before {
      content: "\e6bf";
      font-family: "iconfont";
      color: #fff;
      font-size: 14px;
      line-height: 15px;
    }

    &:disabled + label:before {
      color: #fff;
      border: 1px solid #ccc;
      background: #e7eaf3;
      cursor: not-allowed;
    }
  }
}

