.checkbox-group {
  label:before {
    content: "";
    display: inline-block;
    width: 16px;
    height: 16px;
    position: absolute;
    top: 3px;
    left: 0;
    border-radius: 3px;
    background: #fff;
    border: 1px solid #e9ebfa;
  }

  .checkbox-normal {
    + label:before {
      -webkit-border-radius: 0px;
      -moz-border-radius: 0px;
      border-radius: 3px;
    }

    &:checked + label:before {
      content: "\e93e";
      font-family: "iconfont";
      font-size: 9px;
      text-align: center;
      line-height: 13px;
      background: #3366ff;
      margin: 0 auto;
      text-align: center;
      color: #fff;
    }

    &:disabled + label:before {
      color: #fff;
      border: 1px solid #ccc;
      background-color: #e7eaf3;
      cursor: not-allowed;
    }
  }

  .checkbox-indeterminate + label:before {
    content: "\e95f";
    font-family: "iconfont";
    font-size: 14px;
    text-align: center;
    line-height: 15px;
    color: #fff;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
  }
}

