.multi-select-active:after {
  content: "\e93e";
  font-family: "iconfont";
  margin-left: 64%;
  font-weight: bold;
}

.icon-single-add, .icon-double-add {
  font-size: 22px;
  position: absolute;
  margin-left: -30px;
  margin-top: 7px;
}

.transfer-double {
  width: 100%;
  height: 100%;
  background-color: #fff;
  left: 30%;
  position: inherit;
  z-index: 1000;
  border-radius: 0px;
}

.transfer-double-content-left {
  display: inline-block;
  width: 46%;
  border: 1px solid #d3dfea;
  border-radius: 5px;
  float: left;
  overflow: hidden;
}

.transfer-double-content-middle {
  display: inline-block;
  width: 35px;
  float: left;
  margin: 12% 22px 0 22px;
  text-align: center;
  vertical-align: middle;
}

.transfer-double-content-right {
  display: inline-block;
  width: 46%;
  border: 1px solid #d3dfea;
  border-radius: 5px;
}

.transfer-double-content-tabs {
  display: inline-block;
  width: 100%;
  border-bottom: 1px solid #d3dfea;

  .tab-item-name {
    &.tab-active {
      background: #eaeefa;
    }

    display: inline-block;
    line-height: 36px;
    margin-left: 15%;
    width: 50%;
    cursor: pointer;
    margin: 0;
    font-weight: 500;
    text-align: center;
    padding: 3px;
  }
}

.transfer-double-content-param {
  display: inline-block;
  width: 100%;
  line-height: 36px;
  border-bottom: 1px solid #e7eaf3;
  text-align: center;
  padding: 3px;
  font-weight: 500;

  .param-item {
    display: inline-block;
    width: 40%;
    height: 36px;
    line-height: 36px;
  }
}

.vertical-separation-line {
  color: #eee;
  margin: 0 10px;
}

.transfer-double-list {
  display: none;
}

.tab-content-active {
  display: block;
}

.transfer-double-list-header {
  padding: 8px 8px 6px 8px;
  text-align: center;
}

.transfer-double-list-search-input {
  width: 100%;
  line-height: 24px;
  border: 1px solid #e9ebfa;
  padding: 5px 0 4px 8px;
  border-radius: 4px;
}

.transfer-double-list-content {
  padding: 3px 3px;
}

.transfer-double-list-main {
  height: 210px;
  overflow-y: auto;

  .transfer-double-list-ul {
    list-style: none;
    padding-left: 7px;
    margin: 0;

    .transfer-double-list-li {
      margin-top: 5px;

      input {
        margin-right: 10px;
      }
    }
  }

  .transfer-double-group-list-ul {
    list-style: none;
    padding-left: 7px;
    margin: 0;

    .transfer-double-group-list-li {
      margin-top: 5px;

      .transfer-double-group-list-li-ul {
        list-style: none;
        padding-left: 20px;

        .transfer-double-group-list-li-ul-li {
          margin-top: 5px;
        }
      }
    }
  }
}

.transfer-double-selected-list {}

.transfer-double-selected-list-header {
  padding: 8px 8px 6px 8px;
  text-align: center;
}

.transfer-double-selected-list-search-input {
  width: 100%;
  line-height: 24px;
  color: #777ab2 !important;
  border: 1px solid #e9ebfa;
  padding: 5px 0 4px 8px;
  border-radius: 4px;
}

.transfer-double-selected-list-content {
  padding: 3px 3px;
}

.transfer-double-selected-list-main {
  height: 210px;
  overflow-y: auto;

  .transfer-double-selected-list-ul {
    list-style: none;
    padding-left: 7px;
    margin: 0;

    .transfer-double-selected-list-li {
      margin-top: 5px;

      .checkbox-group {
        width: 65%;
      }

      input {
        &[type="checkbox"] {
          margin-right: 10px;
        }

        &[type="text"] {
          min-width: 40px;
          width: 40px;
          min-height: 24px;
          height: 24px;
        }
      }
    }
  }
}

.transfer-double-list-footer {
  border-top: 1px solid #e9ebfa;
  padding-left: 0px;
  padding-top: 1px;

  span {
    margin-left: 20px;
  }

  input {
    min-width: 40px;
    width: 40px;
    min-height: 24px;
    height: 24px;
    margin-left: 75px;
  }

  .btn-setting {
    display: inline-block;
    margin-left: 5px;
  }

  label {
    margin-left: 10px;
    margin-top: 5px;
  }
}

.transfer-double-footer {
  text-align: right;
}

.btn-select-arrow {
  display: inline-block;
  width: 34px;
  height: 34px;
  line-height: 34px;
  border-radius: 3px;
  color: #424e79;
  background: #eaeefa;
  border: 1px solid #e9ebfa;
  cursor: pointer;

  + .btn-select-arrow {
    margin-top: 10px;
  }
}

