.clearfix {
  &:before {
    display: table;
    content: " ";
  }

  &:after {
    display: table;
    content: " ";
    clear: both;
  }

  *zoom: 1;
}

/*
 * input placeholder style
 */

input {
  &::-webkit-input-placeholder {
    /* WebKit browsers */
    font-size: 12px;
    color: #bbb;
  }

  &:-moz-placeholder {
    /* Mozilla Firefox 4 to 18 */
    font-size: 12px;
    color: #bbb;
  }

  &::-moz-placeholder {
    /* Mozilla Firefox 19+ */
    font-size: 12px;
    color: #bbb;
  }

  &:-ms-input-placeholder {
    /* Internet Explorer 10+ */
    font-size: 12px;
    color: #bbb;
  }
}

/*
 * webkit????????
 */

.transfer-double-list-main, .transfer-double-selected-list-main {
  &::-webkit-scrollbar {
    width: 4px;
    height: 4px;
  }

  &::-webkit-scrollbar-track {
    background: #f6f6f6;
    border-radius: 2px;
  }

  &::-webkit-scrollbar-thumb {
    background: #e7eaf3;
    border-radius: 2px;
  }

  &::-webkit-scrollbar-corner {
    background: #f6f6f6;
  }
}

/** responsive **/
@media (max-width: 480px) {
  .transfer-double-content-left, .transfer-double-content-right {
    width: 100%;
  }

  .transfer-double-content-middle {
    margin: 15px 15px 15px 40%;
  }
}

@media (min-width: 481px) and (max-width: 992px) {
  .transfer-double-content-left {
    width: 100%;
  }

  .transfer-double-content-right {
    Width: 100%;
  }

  .transfer-double-content-middle {
    margin: 15px 15px 15px 40%;
  }

  .transfer {
    padding-right: 0 !important;
  }
}

@media (min-width: 993px) and (max-width: 1366px) {
  .transfer-double-content-left {
    width: 42%;
  }

  .transfer-double-content-right {
    Width: 42%;
  }
}