/* jQuery.countdownTimer.css*/

.countdowntimer {
  text-align: center;
  margin-top: 0.75rem;
}

.style {
  width: 100%;
  font-weight: 400;
  border-style: solid;
}

.colorDefinition {
  background: #000000;
  color: #FFFFFF;
  border-color: #F0068E;
}

.size_xl {
  font-size: 50px;
  border-width: 8px;
  border-radius: 6px;
}

.size_lg {
  font-size: 40px;
  border-width: 7px;
  border-radius: 5px;
}

.size_md {
  font-size: 30px;
  border-width: 5px;
  border-radius: 4px;
}

.size_sm {
  font-size: 20px;
  border-width: 3px;
  border-radius: 3px;
}

.size_xs {
  font-size: 15px;
  border-width: 2px;
  border-radius: 2px;
}

.lang-rtl {
  direction: rtl;
}

.displaySection {
  display: block;
  float: left;
  font-size: 75%;
  text-align: center;
  border-right: 5px solid #F0068E;
}

.timerDisplay > span.displaySection:last-child {
  border-right: none;
}

.label6 .displaySection {
  width: 15%;
}

.label5 .displaySection {
  width: 18%;
}

.label4 .displaySection {
  width: 23.5%;
}

.label3 .displaySection {
  width: 31.8%;
}

.label2 .displaySection {
  width: 48.6%;
}

.label1 .displaySection {
  width: 98%;
}

.numberDisplay {
  font-size: 200%;
}

.periodDisplay {
  display: block;
}

.timerDisplay {
  clear: both;
  width: 100%;
  padding: 8px 4px;
  text-align: center;
  float: left;
}

.labelformat {
  float: left;
  width: 350px;
  font-size: 15px;
  border-width: 7px;
  border-radius: 5px;
}